* {
  box-sizing: border-box;
  font-family: "Roboto", open-sans;
}

body {
  margin: 0;
}

header {
  background: #E4C1AE;
  color: white;
  padding: 1rem;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  width: 100%;
}
header h1 {
  font-size: 4rem;
  color: white;
  margin: 0;
  text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2), 0 20px 20px rgba(0, 0, 0, 0.15);
}

.main-content {
  display: flex;
  justify-content: center;
  width: 1440px;
  margin-top: 95px;
}
.main-content .description {
  width: 380px;
  margin-left: 95px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.main-content .description h1 {
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
  margin: 25px 0;
}
.main-content .description p {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  margin: 0;
  height: 95px;
}
.main-content .description #countBtn {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  width: 130px;
  height: 50px;
  background: #E4C1AE;
  border-radius: 32px;
  color: #FFFFFF;
  border: none;
  margin-top: 35px;
  cursor: pointer;
}
.main-content .description #countBtn:hover {
  color: #E4C1AE;
  border: 1px solid black;
  background: #FFFFFF;
  transition: 0.5s;
}

.modal {
  position: fixed;
  z-index: 100;
  background: white;
  border-radius: 10px;
  width: 50%;
  top: 40%;
  left: 25%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  display: none;
}
.modal.active {
  display: block;
  animation: moveUp 0.5s linear;
}
.modal .close {
  position: absolute;
  top: 20px;
  right: 30px;
  background: none;
  cursor: pointer;
  font-size: 30px;
  border: none;
}
.modal .close:hover {
  color: red;
  transition: 0.3s;
}
.modal .title {
  width: 380px;
  height: 50px;
  margin-left: 85px;
  margin-top: 50px;
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 40px;
}
.modal .text {
  margin-left: 85px;
  margin-bottom: 55px;
  font-weight: 300;
  font-size: 15px;
}
.modal .reset-btn {
  position: absolute;
  bottom: 20px;
  right: 30px;
  background: none;
  cursor: pointer;
  font-size: 30px;
  border-radius: 20px;
  border: #FFFFFF;
  padding: 5px 10px;
}
.modal .reset-btn:hover {
  background-color: greenyellow;
  transition: 0.3s;
}

#backdrop {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
  pointer-events: none;
  display: none;
}
#backdrop.active {
  display: block;
  pointer-events: all;
}

@keyframes moveUp {
  0% {
    opacity: 0;
    transform: translateY(2rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0rem);
  }
}